*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
.row{
    width: 100%;
    height: auto;
    display: inline-block;
    padding-left: 1%;
}
@media screen and (max-width:1000px){
    .row{
        height: auto;
    }
}
.row a{
    width: 23%;
    padding: 1.5%;
    float: left;
    text-decoration: none;
    color:navy;
    font-weight: bold;
}
.row a:hover{
    text-decoration:none;
    color:navy;
    font-weight: bold;
}
.row .unit{
    width: 100%;
    height: 98%;
    border-radius: 20px;
    background-color: rgb(0, 0, 0,0.03);
    text-align: center;
    align-items: center;
    justify-content: center;
    padding: 20px;
    color: rgb(0, 0, 0,0.6);
    border:2px solid rgb(0,0,0,0.2);
    margin: 5px;
}
.row .unit:hover{
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
    text-decoration: none;
}
@media screen and (max-width:1000px){
    .row a{
        width: 48%;
    }
}
@media screen and (max-width:800px){
    .row a{
        width: 98%;
        margin-left:20px ;
    }
}